
#app {
  margin: auto;
  height: 100vh;
}
body {
  overflow: hidden;
  height: 100vh;
}
.head > label {
  height: 60px;
  display: inline-block;
  width: calc(100% / 7 - 4px);
}
.head > label > * {
  width: 98%;
  height: 26px;
}
.head > label > input {
  height: 26px;
}
.head > label > select {
  height: 32px;
}
canvas {
  border: 1px solid #BBB;
}



